export default {
	data: function() {
		return {
			mobile: '',
			msgcode: '',
			password1: '',
			password2: '',
			company_name: '',
			code: '',
			name: '',
			email: '',
			mobileTrue: false,
			msgcodeTrue: false,
			password1True: false,
			password2True: false,
			company_nameTrue: false,
			codeTrue: false,
			nameTrue: false,
			emailTrue: false,
			pwdTrue: false

		}
	},

	methods: {
		//获取短信验证码
		getVerifyCode: function() {
			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Login/sendMsgCode',
				dataType: 'json',
				type: 'post',
				data: {
					mobile: this.mobile
				},
				success: function(e) {

					if(e.status == 1) {

					} else {

					}
					return false;
				}
			})

		},

		//提交表单
		submit: function(ev) {
			ev.preventDefault()
			let that = this
			this.mobile != '' ? this.mobileTrue = true : this.mobileTrue = false
			this.msgcode != '' ? this.msgcodeTrue = true : this.msgcodeTrue = false
			this.password1 != '' ? this.password1True = true : this.password1True = false
			this.password2 != '' ? this.password2True = true : this.password2True = false
			this.company_name != '' ? this.company_nameTrue = true : this.company_nameTrue = false
			this.code != '' ? this.codeTrue = true : this.codeTrue = false
			this.name != '' ? this.nameTrue = true : this.nameTrue = false
			this.email != '' ? this.emailTrue = true : this.emailTrue = false

			this.password1 == this.password2 ? this.pwdTrue = true : this.pwdTrue = false

			//				let ison = 1
			//				that.loginStatus ? ison = 1 : ison = 0
			var pattern = /^1[34578]\d{9}$/;
			var pwd = /^[0-9A-Za-z]{6}$/
			if(this.mobileTrue && this.msgcodeTrue && this.password1True && this.password2True && this.company_nameTrue && this.codeTrue && this.nameTrue && this.emailTrue && this.pwdTrue) {
				$.ajax({
					url: 'http://crane.u-xuan.com/Api/Login/company_register',
					dataType: 'json',
					type: 'post',
					data: {
						'mobile': this.mobile,
						'msgcode': this.msgcode,
						'password1': this.password1,
						'password2': this.password2,
						'company_name': this.company_name,
						'code': this.code,
						'name': this.name,
						'email': this.email,

						//							'code': this.code,
					},
					success: function(e) {

						if(e.status == 1) {
							alert(e.msg)

							that.$router.push('/main');

						} else {
							alert(e.msg)
						}
						return false;
					}
				})
			} else {

				if(that.mobile == '') {
					$('#mobile').focus()
					alert('手机号不能为空')

				} else if(pattern.test(that.mobile) == false) {
					alert('手机号格式不正确')

					$('#mobile').focus()
					return false;
				} else if(that.msgcode == '') {
					alert('验证码不能为空')

					$('#msgcode').focus()

				} else if(that.password1 == '') {
					alert('密码不能为空')

					$('#password1').focus()

				} else if(pwd.test(that.password1) == false) {
					alert('密码格式不正确')

					$('#password1').focus()
					return false;
				} else if(that.password2 == '') {
					alert('请再次输入密码')

					$('#password2').focus()

				} else if(that.password2 !== that.password1) {
					alert('两次密码不一致')

					$('#password2').focus()

				} else if(that.company_name == '') {
					alert('单位名称不能为空')

					$('#company_name').focus()

				} else if(that.code == '') {
					alert('机构代码不能为空')

					$('#code').focus()

				} else if(that.name == '') {
					alert('联系人姓名不能为空')

					$('#name').focus()

				} else if(that.email == '') {
					alert('邮箱不能为空')

					$('#email').focus()

				}

			}

		}
	}
}



