export default {
	data: function() {
		return {
			mobileTrue: false,
			msgcodeTrue: false,
			password1True: false,
			password2True: false,
			pwdTrue: false,
			mobile: '',
			msgcode: '',
			password1: '',
			password2: ''

		}
	},

	methods: {
		//获取短信验证码
		getVerifyCode: function() {
			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Login/sendMsgCode',
				dataType: 'json',
				type: 'post',
				data: {
					mobile: this.mobile
				},
				success: function(e) {

					if(e.status == 1) {

					} else {

					}
					return false;
				}
			})

		},
		submit(ev) {
			ev.preventDefault()
			let that = this
			this.mobile != '' ? this.mobileTrue = true : this.mobileTrue = false
			this.msgcode != '' ? this.msgcodeTrue = true : this.msgcodeTrue = false
			this.password1 != '' ? this.password1True = true : this.password1True = false
			this.password2 != '' ? this.password2True = true : this.password2True = false
			this.password1 == this.password2 ? this.pwdTrue = true : this.pwdTrue = false
			//				let ison = 1
			//				that.loginStatus ? ison = 1 : ison = 0
			var pattern = /^1[34578]\d{9}$/;
			if(this.mobileTrue && this.msgcodeTrue && this.password1True && this.password2True && this.pwdTrue) {
				$.ajax({
					url: 'http://crane.u-xuan.com/Api/Login/person_register',
					dataType: 'json',
					type: 'post',
					data: {
						'mobile': this.mobile,
						'msgcode': this.msgcode,
						'password1': this.password1,
						'password2': this.password2
						//							'code': this.code,
					},
					success: function(e) {

						if(e.status == 1) {
							alert(e.msg)

							that.$router.push('/main');

						} else {
							alert(e.msg)
						}
						return false;
					}
				})
			} else {
				if(that.mobile == '') {
					$('#mobile').focus()
							alert('手机号不能为空')

				} else if(pattern.test(that.mobile) == false) {
							alert('手机号格式不正确')
					
					$('#mobile').focus()
					return false;
				} else if(that.msgcode == '') {
							alert('验证码不能为空')
					
					$('#msgcode').focus()


				} else if(that.password1 == '') {
							alert('手密码不能为空')
					
					$('#password1').focus()


				} else if(that.password2 == '') {
							alert('请再次输入密码')
					
					$('#password2').focus()


				} else if(that.password2 !== that.password1) {
							alert('两次密码不一致')
					
					$('#password2').focus()


				}

			}
		},
	}
}



